import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { NgForm, FormControl } from '@angular/forms';

import { User } from '../signup-form/user';

@Component({
  selector: 'egm-mat-signup-form',
  template: `
  <form novalidate  #f="ngForm" [ngFormOptions]="{updateOn: 'blur'}" (ngSubmit)="onSubmit(f)">
  <mat-card class="shipping-card">
    <mat-card-header>
      <mat-card-title>Signup Information</mat-card-title>
    </mat-card-header>
    <mat-card-content>
      <div class="row">
        <div class="col">
          <mat-form-field class="full-width" floatLabel="auto">
            <input  matInput 
                    placeholder="Your user name"
                    name="username"
                    ngModel
                    required
                    minlength="3" 
                    #userNameCtrl="ngModel"
                    [egmForbiddenName]="'bob'"
                    >
            <!-- <mat-hint align="end">{{userNameCtrl.value?.length}} / 3</mat-hint> -->
            <mat-error *ngIf="userNameCtrl.hasError('required')">
              Username is <strong>required</strong>
            </mat-error>
            <mat-error *ngIf="userNameCtrl.hasError('minlength')">
              Username must has <strong>3</strong> character minimum!
            </mat-error>                     
            <mat-error *ngIf="userNameCtrl.hasError('forbiddenName')">
              Username <strong>'{{userNameCtrl.value}}'</strong> has been forbidden!
            </mat-error>                     
          </mat-form-field>
        </div>
      </div> 
      <div class="row">
        <div class="col" ngModelGroup="password" #userPassword="ngModelGroup" egmPasswordMatch="pwd confirmPwd">
          <mat-form-field class="full-width" floatLabel="auto">
            <input matInput 
              type="password"
              placeholder="Your password"
              name="pwd"
              ngModel
              required
              #pwdCtrl="ngModel"
            >
            <mat-error *ngIf="pwdCtrl.hasError('required')">
              Password is <strong>required</strong>
            </mat-error>
          </mat-form-field>
          <mat-form-field class="full-width" floatLabel="auto">
            <input matInput 
              type="password"
              placeholder="Confirm your password"
              name="confirmPwd"
              ngModel
              required
              #confirmCtrl="ngModel"
            >
            <mat-error *ngIf="confirmCtrl.hasError('required')">
              Confirm Password is <strong>required</strong>
            </mat-error>
          </mat-form-field>
          <div style="font-size: 75%;" *ngIf="!confirmCtrl.hasError('required')">
          <mat-error *ngIf="userPassword.hasError('passwordMatch') && (pwdCtrl.touched || pwdCtrl.dirty) && (confirmCtrl.touched || confirmCtrl.dirty)">
            Password mismatch {{userPassword.errors | json}}<br>
            Password: '{{pwdCtrl.value}}'<br>
            Confirm: '{{confirmCtrl.value}}'
          </mat-error>
          </div>
        </div>
      </div>
      </mat-card-content>
      <mat-card-actions>
        <button mat-flat-button color="primary" [disabled]="f.invalid">Submit</button>
      </mat-card-actions>
      <mat-card-footer>
        <div class="row">
          <div class="col">
            Footer
          </div>
        </div>
      </mat-card-footer>
    </mat-card>
  </form>
  <div>
    <h5>user: {{ user | json }}</h5>
    <h5>form value: {{ f.value | json  }}</h5>    
    <!-- <h5>form valueChanges: {{ f. valueChanges | async | json  }}</h5> -->
    <h5>userPassword value: {{ userPassword.value | json  }}</h5>
    <h5>userPassword errors: {{ userPassword.errors | json  }}</h5>
  </div>  
  `,
  styleUrls: ['./mat-signup-form.component.scss']
})
export class MatSignupFormComponent implements OnInit, AfterViewInit {

  user: User = {
    username: '',
    password: {
      pwd: '',
      confirmPwd: ''
    }
  };
  
  // @ViewChild(NgForm) form: NgForm;

  userNameFormCtrl: FormControl;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    // console.log("form.value: %O", this.form);
    // const fg = this.form.form;
    // const ac = fg.controls['username'];
    // if (ac) {
    //   this.userNameFormCtrl = ac as FormControl;
    // }
    // console.log("userNameFormCtrl: %O", this.userNameFormCtrl);
  }

  onSubmit({ value, valid }: { value: User, valid: boolean }) {
    console.log("form.value: %O form.valid: %s", value, valid);
  }

  identity(in_value: any): any {
    return in_value;
  } 
}
