import { CustomValidators } from '../custom-validators';

@Directive({
  selector: '[egmPasswordMatch]',
  providers: [{ provide: NG_VALIDATORS, useExisting: PasswordMatchDirective, multi: true }]
})
export class PasswordMatchDirective implements Validator, OnInit {
  private validator: ValidatorFn = null;
  
  constructor(@Attribute('egmPasswordMatch') private pwdAndConfirmNames) {
    console.log("PasswordMatchDirective constructor() [egmPasswordMatch]=%O", pwdAndConfirmNames); 

    const arrPwdAndConfirmNames = pwdAndConfirmNames ? pwdAndConfirmNames.split(' ') : [];
    if (arrPwdAndConfirmNames.length !== 2) {
      return;
    }
    this.validator = CustomValidators.passwordMatchValidator(arrPwdAndConfirmNames[0], arrPwdAndConfirmNames[1]);
  }
  ngOnInit(){
    console.log("PasswordMatchDirective ngOnInit() [egmPasswordMatch]=%O", this.pwdAndConfirmNames); 
    
  }
  validate(control: FormGroup): {[key: string]: any} | null {
    if (this.validator != null) {
      return this.validator(control);
    } else {
      return {'passwordMatch': {message: 'ERROR in [egmPasswordMatch] Directive'}};
    }
  }
}
