import { Component, OnInit } from '@angular/core';

import { User } from './user';

@Component({
  selector: 'app-signup-form',
  template: `
<form novalidate (ngSubmit)="onSubmit(f)" #f="ngForm" [ngFormOptions]="{updateOn: 'blur'}">
  <label>
    <span>User name: </span>
    <input
      type="text"
      placeholder="Your user name"
      name="username"
      ngModel
      required
      minlength="3"
      #userNameCtrl="ngModel"
      [egmForbiddenName]="'bob'"
      >    
     <div class="error"> 
      Username: {{userNameCtrl.value}} Valid: {{userNameCtrl.valid}}
    </div> 
    <div *ngIf="f.controls.username?.errors?.required && f.controls.username?.touched" class="error">
      Username is required!
    </div>    
    <div *ngIf="userNameCtrl.errors?.minlength && userNameCtrl.touched" class="error">
      Username must has 3 character minimum!
    </div>
    <div *ngIf="userNameCtrl.errors?.forbiddenName && userNameCtrl.touched" class="error">
      Username '{{userNameCtrl.value}}' has been forbidden!
    </div>
    
  </label>
  <div ngModelGroup="password" #userPassword="ngModelGroup" egmPasswordMatch="pwd confirmPwd">
    <label>
      <span>Password: </span>
      <input
        type="password"
        placeholder="Your password"
        name="pwd"
        ngModel
        required
        minlength="3"
        #pwdCtrl="ngModel"
        >
    </label>
    <label>
      <span>Confirm password: </span>
      <input
        type="password"
        placeholder="Confirm your password"
        name="confirmPwd"
        ngModel
        required
        minlength="3"
        #confirmCtrl="ngModel"
        >
    </label>
    <!-- Password error block WRONG by Indus     
    <div *ngIf="(userPassword.invalid || userPassword.value?.pwd !== userPassword.value?.confirmPwd) && (userPassword.touched)">       
      <div *ngIf = "userPassword.invalid; else nomatch">
        Password needs to be more than 8 characters
      </div>
      <ng-template #nomatch >
        Passwords don't match
      </ng-template>  
    </div> 
    <h5>userPassword value: {{ userPassword.path | json  }}</h5>  -->
    <div *ngIf="userPassword.errors?.passwordMatch && (pwdCtrl.touched || pwdCtrl.dirty) && (confirmCtrl.touched || confirmCtrl.dirty)" class="error">
      Password mismatch {{userPassword.errors | json}}<br>
      Password: '{{pwdCtrl.value}}'<br>
      Confirm: '{{confirmCtrl.value}}' 
    </div>

  </div>

  <button type="submit" [disabled]="f.invalid">Sign up</button>
</form>
<div>
  <h5>user: {{ user | json }}</h5>
  <h5>form value: {{ f.value | json  }}</h5>
  <!-- <h5>usernameCtrl value: {{ f.controls.username?.errors | json  }}</h5> -->
  <h5>usernameCtrl value: {{ userNameCtrl.errors | json  }}</h5>
</div>
  `,
  styles: [`
  html, body {
  margin: 0;
  padding: 0;
}

main { display: block; }

form {margin: 25px;padding: 25px;}

html,
body,
button,
input,
select,
textarea {
  font: 400 16px/1.4 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: #444;
  letter-spacing: 0.5px;
  -webkit-font-smoothing: antialiased;
}

body {
  background: #f1f2f1;
  margin: 50px 0 0;
}

.root {}
.root h1 {
  text-align: center;
}

button[type=submit] {
  width: 100%;
  height: 54px;
  padding: 0;
  font-size: 22px;
  color: white;
  text-align: center;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  background: green;
  border: 0;
  border-bottom: 2px solid #d76b60;
  border-radius: 5px;
  cursor: pointer;
  -webkit-box-shadow: inset 0 -2px #d76b60;
  box-shadow: inset 0 -2px #d76b60;
}
button[type=submit][disabled] {
  background: #f0776c;
}
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
.error {
  font-size: 13px;
  color: #c7254e;
  background: #f9f2f4;
  border-radius: 3px;
  padding: 15px;
  margin: -26px 0 15px;
}
label {
  display: block;
}
span {
  display: block;
  margin: 0 0 10px;
}
input {
  outline: 0;
  width: 100%;
  height: 50px;
  margin-bottom: 25px;
  padding: 0 15px 2px;
  font-size: 17px;
  background: #fff;
  border: 2px solid #ebebeb;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 -2px #ebebeb;
  box-shadow: inset 0 -2px #ebebeb;
  &:focus {
    border-color: #62c2e4;
    outline: none;
    -webkit-box-shadow: inset 0 -2px #62c2e4;
    box-shadow: inset 0 -2px #62c2e4;
  }
}
  `]
})
export class SignupFormComponent implements OnInit {
  user: User = {
    username: '',
    password: {
      pwd: 'sergey.netesa@gmail.com',
      confirmPwd: ''
    }
  };

  constructor() { }

  ngOnInit() {
  }

  onSubmit({ value, valid }: { value: User, valid: boolean }) {
    console.log("form.value: %O form.valid: %s", value, valid);
  }
}