import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroupDirective, NgForm } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material';

export class CustomErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(
    control: FormControl | null,
    form: FormGroupDirective | NgForm | null
  ): boolean {
    // console.log("CustomErrorStateMatcher isErrorState() control: %O form: %O", control, form);

    return !!(control && control.invalid && control.dirty);
  }
}

@Component({
  selector: 'egm-inputs-lesson',
  template: `
  <h2>HTML Input</h2>
  <input type="text" placeholder="HTML Input">
  
  <h2>Basic Input Auto</h2>
  <mat-form-field floatLabel="auto">
    <input matInput
          placeholder="Hello World">
  </mat-form-field>

  <h2>FloatPlaceholder Always</h2><!-- floatPlaceholder="never" устарел!-->
  <mat-form-field floatLabel="always">
    <input matInput
          placeholder="Hello World">
  </mat-form-field>

  <h2>FloatPlaceholder Never</h2>
  <mat-form-field floatLabel="never">
    <input matInput
          type="email"
          placeholder="Hello World">
  </mat-form-field>

  <h2>With TextArea float label from Module (never)</h2>
  <mat-form-field>
    <textarea matInput
              placeholder="Hello World"></textarea>
  </mat-form-field>

  <h2>Inputs with hints</h2>
  <mat-form-field>
    <input matInput
          ngModel
          #demoInput="ngModel"
          placeholder="Hello World">
    <mat-hint>{{demoInput.value?.length}}/30</mat-hint>
  </mat-form-field>

  <h2>Inputs with multiple hints</h2>
  <mat-form-field>
    <input matInput
          placeholder="Hello World">
    <mat-hint align="start">No special characters allowed.</mat-hint>
    <mat-hint align="end">{{demoInput.value?.length}}/30</mat-hint>
  </mat-form-field>

  <h2>Password Input with errors</h2>
  <mat-form-field hideRequiredMarker>
    <input matInput
          [(ngModel)]="password"
          #passwordInput="ngModel"
          placeholder="Password"
          minlength="3"
          maxlength="5"
          required>
    <mat-hint align="start">Password must be atleast 3 characters</mat-hint>
    <mat-hint align="end">{{password.length}}/16</mat-hint>
    <mat-error *ngIf="passwordInput.errors?.required">Password is required.</mat-error>
    <mat-error *ngIf="passwordInput.errors?.minlength">3 characters minimum.</mat-error>
    <mat-error *ngIf="passwordInput.errors?.maxlength">5 characters minimum.</mat-error> <!-- не нужен ! -->
  </mat-form-field>
  
  <!-- <h3>Password: {{passwordInput.errors}}</h3> -->

  <h2>Custom Error Strategies</h2>
  <mat-form-field>
    <input matInput
          [(ngModel)]="name"
          [errorStateMatcher]="customErrorStateMatcher"
          #nameInput="ngModel"
          placeholder="User name"
          minlength="6"
          maxlength="16"
          required>
    <mat-hint align="start">Name must be atleast 6 characters</mat-hint>
    <mat-hint align="end">{{name.length}}/16</mat-hint>
    <mat-error *ngIf="nameInput.errors?.required">Name is required.</mat-error>
    <mat-error *ngIf="nameInput.errors?.minlength">6 characters minimum.</mat-error>
  </mat-form-field>
  `,
  providers: [CustomErrorStateMatcher],
  styleUrls: ['./inputs-lesson.component.scss']
})
export class InputsLessonComponent implements OnInit {
  public password = '';
  public name = '';

  constructor(public customErrorStateMatcher: CustomErrorStateMatcher) {}

  ngOnInit() {}
}
