@Component({
  selector: 'egm-signup-form-matcher',
  templateUrl: './signup-form-matcher.component.html',
  styleUrls: ['./signup-form-matcher.component.scss']
})
export class SignupFormMatcherComponent implements OnInit {
  userFG: FormGroup;
  
  parentErrorStateMatcher = new ParentErrorStateMatcher();

  constructor(private fb: FormBuilder) { }

  // Accessors
  get username() { return this.userFG.get('username'); }  
  get password() { return this.userFG.get('password'); }
  get pwd() { return this.password.get('pwd'); }
  get confirmPwd() { return this.password.get('confirmPwd'); }

  ngOnInit() {
    this.userFG = this.fb.group({
      username: ['', [
        Validators.required, 
        Validators.minLength(3), 
        CustomValidators.forbiddenNameValidator(/^bob$/i)
      ]],
      password: this.fb.group({
        pwd: ['', Validators.required],
        confirmPwd: ['', Validators.required]
      }, 
      // { validators: pwdMatchValidator }
      { validators: CustomValidators.passwordMatchValidator('pwd', 'confirmPwd') }
      )
    }, { updateOn: 'blur' }); 
  }

  onSubmit({ value, valid }: { value: User, valid: boolean }) {
    console.log("form.value: %O form.valid: %s", value, valid);
  }
}
