import { Component, OnInit, OnDestroy, HostListener } from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { BreakpointObserver } from '@angular/cdk/layout';
import { Router, ActivatedRoute, Routes, NavigationEnd } from '@angular/router';

import { Observable } from 'rxjs/Observable';
import { pluck, map, filter, shareReplay, tap } from 'rxjs/operators';

import { LessonConfigService } from './shared/lesson-config.service';
import { LESSON_ROUTES, LessonRoutes } from './lessons/lessons.routes';

@Component({
  selector: 'egm-app',
  /* template: `<!-- WRONG use *ngSubscribe or *ngIf in the mat- elements! -->
  <mat-sidenav-container class="lesson-list-container"
        *ngSubscribe=" lessonConfig.shouldUseStandardStyles as isUseStandardStyles">

    <mat-sidenav [mode]="sidenavMode"
                [opened]="!isSmallScreen"
                [fixedInViewport]="true"
                class="lesson-list-sidenav"
                #lessonSidenav>

      <mat-toolbar color="primary">Egghead Material</mat-toolbar>

      <mat-nav-list>
        <a mat-list-item
          *ngFor="let lesson of lessons; let i = index"
          [routerLink]="lesson.path"
          routerLinkActive="active-lesson">
          {{i + 1}} - {{ lesson.data.shortName }}
        </a>
      </mat-nav-list>

    </mat-sidenav>

    <mat-toolbar color="accent"
                *ngIf="isUseStandardStyles"> {{ header$ | async }} </mat-toolbar>

    <div class="mat-typography"
        [class.lesson-wrapper]="isUseStandardStyles">

      <router-outlet></router-outlet>

    </div>

  </mat-sidenav-container>

  <button *ngIf="isSmallScreen"
          mat-mini-fab
          color="primary"
          class="sidenav-toggle"
          [@growInOut]="'in'"
          (click)="lessonSidenav.toggle()">

    <mat-icon>view_list</mat-icon>

  </button>
  `, */
  templateUrl: './app.component.html',
  animations: [
    trigger('growInOut', [
      state('in', style({ opacity: 1 })),
      transition('void => *', [
        style({
          opacity: 0,
          transform: 'scale3d(.3, .3, .3)'
        }),
        animate(`150ms ease-in`)
      ]),
      transition('* => void', [
        animate(
          `150ms ease-out`,
          style({
            opacity: 0,
            transform: 'scale3d(.3, .3, .3)'
          })
        )
      ])
    ])
  ],
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {
  public lessons = LESSON_ROUTES;
  public routeMap = createRouteMap(LESSON_ROUTES);
  public header$: Observable<string>;
  public isSmallScreen: boolean;

  constructor(
    public lessonConfig: LessonConfigService,
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private _breakpointObserver: BreakpointObserver
  ) {
    this.header$ = _router.events
      .pipe(
        filter(e => e instanceof NavigationEnd),
        map(_ => this.routeMap[_router.url.replace('/', '')] || 'Unknown Lesson'),
        shareReplay(1)
      );
  }

  ngOnInit() {
    console.log('ENTER AppComponent ngOnInit()');

    this._breakpointObserver
      .observe(['(max-width: 901px)'])
        .pipe(
          tap(vvv => console.log('AppComponent ngOnInit() : %O', vvv)),
          pluck('matches')
          )
      .subscribe((m: boolean) => {
        console.log('AppComponent ngOnInit() (max-width: 901px): %s', m);
        this.isSmallScreen = m;
        },
        (err) => { console.log('AppComponent ngOnInit() error -> %O', err)},
        () =>  { console.log('AppComponent ngOnInit() onComplite') }
       );

  }
  ngOnDestroy() {
    console.log('AppComponent  ngOnDestroy() isSmallScreen: %s', this.isSmallScreen);

  }
  get sidenavMode() {
    return this.isSmallScreen ? 'over' : 'side';
  }
}

export function createRouteMap(routes: LessonRoutes) {
  return routes.reduce((acc: { [key: string]: string }, route) => {
    return { ...acc, [route.path]: route.data.lessonName };
  }, {});
}
